*{font-family:'Open Sans', sans-serif}body{margin:0;padding:0}.footer{background:linear-gradient(135deg, #00384C 0%, #001821 100%);color:#ecf0f1;width:100%;display:flex;justify-content:center;padding:60px  0 60px 0}.footer .container-footer{display:flex;flex-direction:column;justify-content:center;align-items:center}.footer .container-footer .wrapper{display:flex;flex-direction:row;margin-bottom:80px}.footer .container-footer .wrapper .main{margin:20px 80px;display:flex;flex-direction:column;justify-content:center;align-items:center}.footer .container-footer .wrapper .main span{text-transform:uppercase;margin-bottom:30px;text-align:center}.footer .container-footer .wrapper .main img{margin:0 16px}@media screen and (max-width: 400px){.footer .container-footer .wrapper{flex-direction:column}}@media screen and (max-width: 770px){.footer .container-footer .wrapper{flex-direction:column}}.footer .container-footer .footerText{display:flex;flex-direction:column;align-items:center;font-size:12px;max-width:900px;line-height:15px;margin:25px;text-align:center}.footer .container-footer .footerText div{display:flex;flex-direction:column;align-items:center}.footer .container-footer .divider{background-color:#00BFFF;margin:30px;width:90%;max-width:1200px}.footer .container-footer .rights{font-size:16px;text-align:center}

